.cult-card {
  width: 70%;
  position: relative;
  padding-left: 70px;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  padding-top: 36px;
  padding-bottom: 36px;
}

.cult-banner {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  position: absolute;
  left: -60px;
  top: 30%;
  object-fit: cover;
}

.cult-title {
  font-family: "Poppins", sans-serif;
  font-size: 42px;
  font-weight: 700;
  color: #801d5c;
}
.cult-desc {
  font-family: "Courgette", cursive;
  font-size: 28px;
  font-weight: 500;
  padding-right: 12px;
}
@media only screen and (max-width: 600px) {
  .cult-card {
    width: 100%;
    padding-left: 4px;
    padding-top: 70px;
    margin-top: 40px;
  }
  .cult-banner {
    height: 120px;
    width: 120px;
    border-radius: 50%;
    position: absolute;
    top: -60px;
    left: 30%;
    object-fit: cover;
  }

  .cult-title {
    font-size: 32px;
  }
  .cult-desc {
    font-size: 20px;
  }
}
